import React from "react";
import {
  VStack,
  Text,
  UnorderedList,
  ListItem,
} from "@chakra-ui/react";

import { DownloadIcon } from "@chakra-ui/icons"

import pdfDatenschutz from "../documents/DatenschutzinfosFuerMitglieder.pdf";
import odtDatenschutz from "../documents/DatenschutzinfosFuerMitglieder.odt";

import pdfBeitrittserklaerung from "../documents/Beitrittserklaerung.pdf";
import odtBeitrittserklaerung from "../documents/Beitrittserklaerung.odt";

import pdfSicherheitsregelnImDojo from "../documents/SicherheitsregelnImDojo.pdf";
import odtSicherheitsregelnImDojo from "../documents/SicherheitsregelnImDojo.odt";

function Formulare() {
  return (
    <VStack alignItems="start">
      <Text fontSize="2xl">
        Formulare
      </Text>
      <Text>
        Die Dateien liegen im PDF- und im odt-Format.
        Die Pdf-Dateien sollten von jeden modernen Browser angezeigt werden können.
      </Text>
      <UnorderedList pl="6">
        <ListItem>
          <a href={pdfDatenschutz} download>
            Datenschutz für Mitglieder (pdf)
            <DownloadIcon ml="2"/>
          </a>
        </ListItem>
        <ListItem>
          <a href={odtDatenschutz} download>
            Datenschutz für Mitglieder (odt)
            <DownloadIcon ml="2"/>
          </a>
        </ListItem>
        <ListItem mt="2">
          <a href={pdfBeitrittserklaerung} download>
            Beitrittserklärung (pdf)
            <DownloadIcon ml="2"/>
          </a>
        </ListItem>
        <ListItem>
          <a href={odtBeitrittserklaerung} download>
            Beitrittserklärung (odt)
            <DownloadIcon ml="2"/>
          </a>
        </ListItem>
        <ListItem mt="2">
          <a href={pdfSicherheitsregelnImDojo} download>
            Sicherheitsregeln Im Dojo (pdf)
            <DownloadIcon ml="2"/>
          </a>
        </ListItem>
        <ListItem>
          <a href={odtSicherheitsregelnImDojo} download>
            Sicherheitsregeln Im Dojo (odt)
            <DownloadIcon ml="2"/>
          </a>
        </ListItem>
      </UnorderedList>
    </VStack>
  );
};

export default Formulare;